.container-about {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #fff;
  overflow-x: hidden;
}

.top-img-about {
  position: relative;
  z-index: 0;
  max-width: 100%;
  height: auto;
  margin-top: -2%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  overflow: hidden;
}

.bottom-card-about {
  position: absolute;
  top: 50%;
  width: 100%;
  height: auto;
  min-height: 100%;
  padding-bottom: 5%;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 1;
  justify-content: flex-start;
  padding: 5%;
  background-color: #fff;
  overflow: hidden;
}

@media (max-width: 768px) {
  .top-img-about {
    position: relative;
    z-index: 0;
    width: auto;
    height: auto;
    -webkit-transform: scale(1.5);
    -moz-transform: scale(1.5);
    -o-transform: scale(1.5);
    margin-top: 10%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    overflow: hidden;
  }
  .bottom-card-about {
    position: absolute;
    top: 30%;
    width: 100%;
    height: auto;
    min-height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 1;
    justify-content: space-around;
    padding: 5%;
    background-color: #fff;
    overflow: hidden;
  }
  /* //for main rows to be responsive */
  .responsive {
    padding-left: 10px;
    padding-right: 10px;
    display: flex;
    flex-direction: column !important;
  }
}

.row {
  display: flex;
  flex-direction: row;
  padding: 1%;
  width: 100%;
  height: 100%;
  align-items: flex-start;
  justify-content: flex-start;
}

.column {
  display: flex;
  flex-direction: column;
  padding-left: 5%;
  align-items: flex-start;
  justify-content: flex-start;
  width: 100%;
}

.gray {
  background-color: #eee;
}

.prof-pic {
  width: 40%;
  height: auto;
}

.avatar {
  vertical-align: middle;
  width: 80px;
  min-height: 80px;
  border-style: solid;
  border-width: 3px;
  /* padding: 5px; */
  border-color: #eee;
  border-radius: 50%;
}

@media (max-width: 768px) {
  .avatar {
    vertical-align: middle;
    width: 60px;
    min-height: 60px;
    border-style: solid;
    border-width: 3px;
    /* padding: 5px; */
    border-color: #eee;
    border-radius: 50%;
  }
  .column {
    display: flex;
    flex-direction: column;
    padding: 10px;
    align-items: flex-start;
    justify-content: flex-start;
    width: 100%;
  }
}

.column h4 {
  margin: 0;
}

.column p {
  margin: 0;
  margin-top: 5px;
}

.left-padding {
  padding-left: 15px;
}

.divider {
  border-right: solid 2px #eee;
}